.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  /* pointer-events: none; */
  position: relative;
  top: 0;
  left: 0;
}

.Gear {
  height: 15vmin;
  position: absolute;
  left: 12vmin;
  top: 28vmin;
}
.Gear2 {
  height: 12vmin;
  position: absolute;
  left: 22vmin;
  top: 19.8vmin;
}

.parent {
  position: relative;
  top: 0;
  left: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s alternate;
  }
  .Gear {
    animation: Gear-spin infinite 10s linear;
  }
  .Gear2 {
    animation: Gear2-spin infinite 10s linear;
    /* animation-delay: 440ms; */
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(-3deg) scale3d(1.05, 1, 1);
  }
  to {
    transform: rotate(0deg) scale3d(1, 1.05, 1);
  }
}

@keyframes Gear-spin {
  from {
    transform: rotate(15deg);
  }
  to {
    transform: rotate(375deg);
  }
}
@keyframes Gear2-spin {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}
